<template>
  <div class="upload-homepage-img">
    <div class="upload-content">
      <upload-area
        v-loading="loading"
        :element-loading-text="`上传${(progress * 100).toFixed(0)}%`"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.6)"
        accept=".gif, .jpg, .jpeg, .png"
        :max-size="maxSize"
        :disabled="!$can('system', 'OP0007')"
        @upload="handleUploadFile">
        <!-- <upload-style :url="img" /> -->
        <div class="upload-style">
          <i class="iconfont icon-upload-cloud-o" />
          <p class="upload-text" v-if="sizeTips">
            {{ sizeTips }}</p>
        </div>
      </upload-area>

      <!-- <p class="tip">{{ $t('profile.info.upload_company_logo_tip') }}</p> -->
    </div>
  </div>
</template>

<script>
import { i18n } from '@/i18n';
import UploadArea from '@/components/upload';
// import UploadStyle from '@/components/upload/styles/UploadStyle1';

import { OSSUploader } from '@/utils/oss/uploader';

export default {
  components: {
    UploadArea,
  },
  dialogify: {
    confirmEvent: 'upload',
    props: {
      width: '460px',
      height: '290px',
      top: '30vh',
      center: true,
      title: i18n.t('supplier-backend.homepage.change_cover'),
    },
  },
  props: {
    url: {
      type: String,
      required: true,
    },
    maxSize: {
      type: Number,
      default: 4 * 1024 * 1024,
    },
    sizeTips: {
      type: String,
      default: i18n.t('supplier-backend.homepage.upload_img_tip', ['2560*1178px', '4MB']),
    },
  },
  data() {
    return {
      img: this.url,
      loading: false,
      progress: '',
    };
  },
  watch: {
    url(value) {
      this.img = value;
    },
  },
  methods: {
    // 处理上传图片
    async handleUploadFile(file) {
      // 生成 img 地址，并显示在页面上
      // const imgUrl = window.URL.createObjectURL(file);
      // this.img = imgUrl;

      // 把图片上传到OSS
      this.loading = true;
      try {
        // const path = await uploadBlobToOSS(file, 'BRAND_IMG');
        // 上传到oss
        const { url } = await OSSUploader.send({
          sourceType: 'SUPPLIER_HOME_PAGE',
          body: file,
          onUpload: (event, fileLoaded, fileSize, progress) => {
            this.progress = progress;
          },
        });
        // 按规定格式传给父组件
        this.$emit('upload', { field: 'img', value: url });
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

